import React from "react";
import './Thanks.css';

function Thanks(props){
    return(
        <div className="thanks text-center">
            <div className="card-header title-header-for-popup-thanks p-0 m-0 border-0">
                <p className="text-center p-0 border-0">Thank You</p>

            </div>
            <p className="text-center">Thank you for your assessments</p>
            <button type="button" className="btn btn-outline-warning btnClose" onClick={props.click}>
                <i className="fa fa-times" aria-hidden="true"></i>
            </button>
        </div>
    )
}

export default Thanks;
