import React from "react";
import './Message.css'
function Message (props){
  return(
      <div className="message col">
          <div className="card-header title-header-for-popup-message p-0 m-0 border-0">
              <p className="text-center p-0 border-0 m-0">Assessments</p>
          </div>
          <p>Ratings: {props.star} star</p>
          <p>Please give us a comment !</p>
          <textarea id="myTextarea" className="p-2" onChange={props.change}></textarea>
          <div className="d-flex" >
              <button type="button" className="mx-auto btn btn-outline-primary" data-mdb-ripple-color="dark" onClick={props.clickTheOpenPopupThanks}>Send comment</button>
              {/*<button type="button" className="btn btn-outline-secondary" onClick={props.click}>Close</button>*/}


          </div>
          <button type="button" className="btn btn-outline-warning btnClose" onClick={props.click}>
              <i className="fa fa-times" aria-hidden="true"></i>
          </button>
      </div>
  )
}

export default Message;
