import img1 from './images/img1.jpg';
import img2 from './images/img2.jpg';
import img3 from './images/img3.jpg';
import img4 from './images/img4.jpg';
import img5 from './images/img5.jpg';
import img6 from './images/img6.jpg';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Picture from "./component/Picture/Picture";
import {useState} from "react";
import '../node_modules/font-awesome/css/font-awesome.min.css';
import Modal from "react-modal";
import Message from "./component/Message/Message";
import Thanks from "./component/Thanks/Thanks";

function App() {
  let [arrayPicture, setArayPicture] = useState([
      {url:img1, name: "Madame X",author:"Wuyi",date:"22-08-1998" ,id : "img1", starNum : 0},
      {url:img2, name: "Girl with a Pearl Earring",author:"Dz.Wuyi",date:"01-07-1998", id : "img2", starNum : 0},
      {url:img3, name: "Dora Maar with Cat",author:"VTT",date:"09-04-1999", id : "img3", starNum : 0},
      {url:img4, name: "Flaming June",author:"Wuyi Vu",date:"31-12-2020" ,id : "img4", starNum : 0},
      {url:img5, name: "The Birth of Venus",author:"Vu Thu Thuy",date:"03-05-2021", id : "img5", starNum : 0},
      {url:img6, name: "Ahri Sona",author:"VTTT",date:"15-08-2021", id : "img6", starNum : 0},
  ])
  let [totalStar, setTotalStar] = useState(0);

  //Hàm tính tổng số sao
  const sumStar = () =>{
      let starNum = 0;
      arrayPicture.forEach(function sum(pic){
          starNum = pic.starNum+ starNum;
      });
      setTotalStar(starNum);
    }

  //Hàm đánh giá
  const voteStar = (idStarOfPic,indexOfPic,indexOfStar) =>{
      //Bỏ hoàn toàn sao khi nhấn lại vào vị trí sao đã nhấn
      let colorNextStar ;
      if(indexOfStar == 4){
          //Nếu đây là vị trí lớn nhất thì vị trí sao tiếp sẽ bằng vị trí lớn nhất
           colorNextStar = document.getElementById(idStarOfPic+indexOfStar).style.color;
      } else {
           colorNextStar = document.getElementById(idStarOfPic+(indexOfStar+1)).style.color;
      }

      let colorStarPresent = document.getElementById(idStarOfPic+indexOfStar).style.color;
      if((colorNextStar != "yellow" && colorStarPresent == "yellow") || (colorStarPresent==colorNextStar && colorStarPresent=="yellow" && indexOfStar==4)){
          for( let i = indexOfStar; i>=0; i--){
              document.getElementById(idStarOfPic+i).style.color='black';
          }
          arrayPicture[indexOfPic].starNum=0;
          sumStar();
          return;
      }//end việc check ấn vào sao đã ấn


      //Style khi ấn vào icon ngôi sao
      for(let i = 0; i<=indexOfStar; i++){
          document.getElementById(idStarOfPic+i).style.color='yellow';
      }
      for(let i = 4; i>indexOfStar; i--){
          document.getElementById(idStarOfPic+i).style.color='black';
      } //end việc style khi ấn vào sao

      //Tính lại số sao đã đánh giá
      arrayPicture[indexOfPic].starNum=indexOfStar+1;
      sumStar();
  }

  let arrButton = [1,2,3,4,5];

  let listPicture = <div className="row picture-list">
      {
      arrayPicture.map((picture,indexOfPic) => {
          return(
              <div className="col-4 picture-detail">
                  <Picture src={picture.url} name={picture.name} author={picture.author} date={picture.date} indexOfPic={indexOfPic}>
                      {
                          arrButton.map((btn,index)=>{
                              return(
                                  <button type="button" onClick={()=>voteStar(picture.id +'star',indexOfPic,index)} className="star btnrating mb-5 btn btn-default btn-lg" id={picture.id +'star'+index}  >
                                      <i className="fa fa-star" aria-hidden="true"></i>
                                  </button>
                              );
                          })
                      }
                  </Picture>
              </div>
          )
      })
      }
      </div>

    const [isOpenPopupSendMessage, setIsOpenPopupSendMessage] = useState(false);
    const [isOpenPopupThank, setIsOpenPopupThank] = useState(false);
    const [comment, setComment] = useState("");
    // const [comment, setComment] = useState(false);
    // const [comment, setComment] = useState(false);
    function toggleModal() {
        setIsOpenPopupSendMessage(!isOpenPopupSendMessage);
    }
    function closeThenOpenThanks(){
        setIsOpenPopupSendMessage(!isOpenPopupSendMessage);
        setIsOpenPopupThank(!isOpenPopupThank);
        setComment(document.getElementById("myTextarea").value);
        document.getElementById("your-comment").style.display = "block";
    }
    function changeComment(){
        setComment(document.getElementById("myTextarea").value);
    }
    function closePopupThanks() {
        setIsOpenPopupThank(!isOpenPopupThank);
    }



  return (
    <div className="App">
      <div className="card-header p-0 border-0">
          <h1 className="header-title text-center text-danger m-0">Wuyi Art</h1>
          <p className="text-center font-style-for-content-header mb-0">It took me 4 years to paint like Raphael,</p>
          <p className="text-center font-style-for-content-header mt-0 pb-3">but a lifetime to paint like a child</p>
      </div>
      <div className="card-content wuyi-art-max-width mt-3 border-0">
          <div className="border title-content  mx-auto mb-4 box">
              <div className="content">
                  <p className="m-0 p-2">Let's enjoy the following works of art 💝</p>
              </div>
          </div>
          {listPicture}
      </div>
      <div className="card-footer text-center border-0 wuyi-art-max-width">
          <p className="font-style-for-title-content text-center mb-1">Let's evaluate the following works of art !</p>
          <p className="font-style-for-title-content text-center"> All star: <span className="style-for-totalStar">{totalStar}</span> </p>
          <button type="button" className="button-submit mb-3" onClick={toggleModal}>Submit an assessment</button>
          <div id="your-comment" className="mx-auto">
              <p className="px-2 pt-2 m-0">Your comment:</p>
              <p className="cmt px-2 pb-2">{comment}</p>
          </div>
      </div>

        <Modal
            isOpen={isOpenPopupSendMessage}
            onRequestClose={toggleModal}
            contentLabel="My dialog"
            className="mymodal"
            overlayClassName="myoverlay"
            closeTimeoutMS={500}
            shouldCloseOnOverlayClick={false}

        >
            <Message star={totalStar} click={toggleModal} clickTheOpenPopupThanks={closeThenOpenThanks}></Message>
        </Modal>

        <Modal
            isOpen={isOpenPopupThank}
            onRequestClose={closeThenOpenThanks}
            contentLabel="My dialog"
            className="mymodal"
            overlayClassName="myoverlay"
            closeTimeoutMS={500}
            shouldCloseOnOverlayClick={false}
        >
            <Thanks click={closePopupThanks}></Thanks>
        </Modal>

    </div>
  );
}

export default App;
