import React, {useState} from "react";
import imgS1 from '../../images/img1-1.jpg';
import imgS2 from '../../images/img2-1.jpg';
import imgS3 from '../../images/img3-1.jpg';
import imgS4 from '../../images/img4-1.jpg';
import imgS5 from '../../images/img5-1.jpg';
import imgS6 from '../../images/img6-1.jpg';
import './Picture.css'
function Picture(props){
    // const logo = require('../../images/img2.jpg');
    let [style, setStyle] = useState(
        {
            backgroundImage: "",
        }
    )
    const changeBGWhenHover = ()=>{
        if(window.innerWidth < 750){
            return;
        }
        if(props.indexOfPic == 0){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS1+"')";
            setStyle({
                backgroundImage: "url('"+imgS1+"')",
            })
        }
        if(props.indexOfPic == 1){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS2+"')";
            setStyle({
                backgroundImage: "url('"+imgS2+"')",
            })
        }
        if(props.indexOfPic == 2){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS3+"')";
            setStyle({
                backgroundImage: "url('"+imgS3+"')",
            })
        }
        if(props.indexOfPic == 3){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS4+"')";
            setStyle({
                backgroundImage: "url('"+imgS4+"')",
            })
        }
        if(props.indexOfPic == 4){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS5+"')";
            setStyle({
                backgroundImage: "url('"+imgS5+"')",
            })
        }
        if(props.indexOfPic == 5){
            // document.getElementById(props.indexOfPic+'pic').style.backgroundImage="url('"+imgS6+"')";
            setStyle({
                backgroundImage: "url('"+imgS6+"')",
            })
        }
    }

    return(
        <div className="align-middle text-center">
            <div className="container1 mx-auto">
                <img className="picture mx-auto" src={props.src} onMouseEnter={changeBGWhenHover} alt="wuyi art" width="300" height="425" />
                <div id={props.indexOfPic+'pic'} style={style} className="overlay mx-auto"></div>
            </div>
            <p className="picture-name">{props.name}</p>
            <div className="picture-info">
                <p className="">Author: {props.author}</p>
                <p className="">Publish date: {props.date}</p>
            </div>
            <div>{props.children}</div>


        </div>
    )
}
export default Picture;
